import React, { useState } from 'react';
import axios from 'axios';
import { z } from 'zod';
import { Toaster, toast } from 'sonner';
import {
  HeaderButton,
  HeaderButtonArrow,
  HeaderHeroContainer,
  HeaderHeroContainerLeft,
  HeaderHeroMainTitle,
  HeaderHeroSubTitle,
  HeaderImgTshorts,
  HeaderInput,
  HeaderInputContainer,
  PurpleSpan,
  StrongSpan,
} from './HeaderHero.styled';
import header_tshorts from '../../../images/heroPicture.png';
import headerArrow from '../../../images/headerArrow.png';
import price from '../../../images/Price.pdf';
const formSchema = z.object({
  name: z.string().optional(),
  contact: z.string().email('  email-'),
  type: z.enum(['price']).default('price'),
});
const HeaderHero = () => {
  const [formData, setFormData] = useState({
    name: '',
    contact: '',
    type: 'price',
  });

  const handleChange = e => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const parseResult = formSchema.safeParse(formData);
    if (!parseResult.success) {
      parseResult.error.errors.forEach(err => toast.error(err.message));
      return;
    }
    try {
      await axios.post('http://localhost:5000/send', formData);

      toast.success('  !');

      setFormData({
        name: '',
        contact: '',
      });

      const downloadLink = document.createElement('a');
      downloadLink.href = price;
      downloadLink.download = price; 
      downloadLink.click();
    } catch (error) {
      console.error(error);
      toast.error('!    ! ');
    }
  };
  return (
    <HeaderHeroContainer>
      <HeaderHeroContainerLeft>
        <HeaderHeroMainTitle>
          -   
          <br />
          <StrongSpan>
            <PurpleSpan> 1/</PurpleSpan>
          </StrongSpan>
        </HeaderHeroMainTitle>
        <HeaderHeroSubTitle>
            ! <br />
           !
        </HeaderHeroSubTitle>
        <form onSubmit={handleSubmit}>
          <HeaderInputContainer>
            <HeaderInput
              placeholder=":"
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
            />
            <HeaderInput
              placeholder=" :"
              type="text"
              name="contact"
              value={formData.contact}
              onChange={handleChange}
            />
          </HeaderInputContainer>
          <Toaster position="top-center" richColors />
          <HeaderButton type="submit">
              <HeaderButtonArrow src={headerArrow} alt="headerArrow" />
          </HeaderButton>
        </form>
      </HeaderHeroContainerLeft>
      <HeaderImgTshorts src={header_tshorts} alt={header_tshorts} />
    </HeaderHeroContainer>
  );
};

export default HeaderHero;
