import React, { useState } from 'react';
import { z } from 'zod';
import axios from 'axios';
import { Toaster, toast } from 'sonner';
import {
  CircleGradient,
  DarkContainer,
  DownloadButton,
  DownloadButtonArrow,
  DownloadYellowText,
  ImgArrow,
  InputContainer,
  SuccessBlackText,
  SuccessContainer,
  SuccessInput,
  SuccessWhiteSubText,
  SuccessWhiteText,
} from './Success.styled';
import whiteArrow from '../../images/whiteArrow.png';

const formSchema = z.object({
  name: z.string().min(1, "' '").max(50, ' 50 '),
  contact: z
    .string()
    .min(1, " '")
    .regex(
      /^\+?[1-9]\d{1,14}$/,
      '       (, +380123456789)'
    ),
  type: z.enum(['consultation', 'other']).default('consultation'),
});

const Success = () => {
  const [formData, setFormData] = useState({
    name: '',
    contact: '',
    type: 'consultation',
  });

  const handleChange = e => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const parseResult = formSchema.safeParse(formData);
    if (!parseResult.success) {
      parseResult.error.errors.forEach(err => toast.error(err.message));
      return;
    }

    try {
      await axios.post('http://localhost:5000/send', formData);
      toast.success('  !');
      setFormData({
        name: '',
        contact: '',
        type: 'consultation',
      });
    } catch (error) {
      console.error(error);
      toast.error('!    ! ');
    }
  };
  return (
    <SuccessContainer>
      <SuccessBlackText> </SuccessBlackText>
      <DarkContainer>
        <SuccessWhiteText>
               -.
        </SuccessWhiteText>
        <SuccessWhiteSubText>
             ,       
            -  .      
                   
                    
             .
        </SuccessWhiteSubText>
        <DownloadYellowText> </DownloadYellowText>
        <form onSubmit={handleSubmit}>
          <InputContainer>
            <SuccessInput
              placeholder="..."
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
            />
            <SuccessInput
              placeholder=" ..."
              type="text"
              name="contact"
              value={formData.contact}
              onChange={handleChange}
            />
            <Toaster position="top-center" richColors />
            <DownloadButton type="submit">
               
              <DownloadButtonArrow>
                <ImgArrow src={whiteArrow} alt="dsad" />
              </DownloadButtonArrow>
            </DownloadButton>
          </InputContainer>
        </form>
      </DarkContainer>
      <CircleGradient />
    </SuccessContainer>
  );
};

export default Success;
